.navbar {
  height: 10vh;
  background: #1b1919;
  display: flex;
  justify-content: center;
  align-items: center;


}

.navbar-logo {
  color: white;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 10rem;
  margin-left: 80px;
  margin-bottom: 20px;
}

.nav-items {
  display: flex;
  list-style: none;
  text-align: center;
  width: 65vw;
  justify-content: flex-end;
  margin-right: 5rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 10vh;
}

.nav-item a {
  text-decoration: none;
  color: white;
  font-size: 1.1rem;
  margin-right: 2rem;
  padding: 6px 1px;
  border-radius: 5px;
}

.nav-item a:hover {
  background: red
}
